/* tool bar */
.Home {
  position: relative;

  margin: 0;
  padding: 0;

  box-sizing: border-box;
  width: 100%;
  height: 2800px;

  font-size: 15px;
  font-family: 'Roboto', sans-serif;
  color: var(--font1);
  background: linear-gradient(180deg, var(--bg2), var(--bg3), var(--bg1));
}



/* main */
#main {
  position: relative;
  top: var(--header_space);
  left: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;

  box-sizing: border-box;

  width: calc(100% - 40px);
  
  & > div {
      box-sizing: border-box;

      margin: 20px 0;

      width: calc(100% - 20px);
      height: 620px;

      border-radius: 30px;

      background-color: rgb(36, 0, 69);
  }
}

#b0 {

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  height: 1000px;

  & h1 {
      text-align: center;
      font-size: 68px;
  }
  & ul {
      display: flex;
      justify-content: space-between;

      /* padding: 0 auto; */
      margin: 0 auto;
      padding: 0;

      width: 60%;

      list-style: none;
      font-size: 14px;

      & li {
          display: flex;
          justify-content: center;
          align-items: center;


          box-sizing: border-box;

          padding: 10px;

          width: calc(25% - 10px);
          height: 100px;

          border-radius: 10px;

          background-color: rgb(54, 0, 91);
      }
  }
}

#b1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  & video {
    
      width: 640px;
      height: 480px;
  }
}

#b2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  & video {
      width: 640px;
      height: 480px;
  }
}

@keyframes scroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}

#b3 {
  display: flex;
  justify-content: space-around;
  align-items: center;

  & h1 {
      text-align: center;
      font-size: 68px;
  }

  & ul {
      box-sizing: border-box;

      padding: 0 20px;

      width: 30%;

      border: 1px solid white;

      list-style: none;
      font-size: 30px;
      
      overflow: hidden;
      
      li {
        /* position: relative; */

        display: flex;
        justify-content: space-between;
        align-items: center;


        box-sizing: border-box;
        margin: 20px auto;
        padding: 0 20px;

    
        width: 100%;
        height: 80px;
    
        border-radius: 10px;
    
        font-size: 30px;
        background-color: rgb(69, 67, 85);

        animation: scroll 5s linear infinite;

        & img {
          width: 50px;
          height: 50px;
      
          object-fit: contain;
        }
      }
  }
}

